import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import { useJmdictStore } from '@/stores/jmdict'
import { useJlptStore } from '@/stores/jlpt'

const router = createRouter({
  // history: createWebHistory(import.meta.env.BASE_URL),
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      path: '/Dictionary',
      name: 'Dictionary',
      component: () => import('@/views/DictionaryView.vue'),
      children: [
        {
          path: ':word',
          name: 'Word',
          component: () => import('@/views/WordView.vue')
        }
      ]
    },
    {
      path: '/Lists',
      name: 'Lists',
      component: () => import('@/views/ListsView.vue'),
      children: [
        {
          path: 'JLPT',
          name: 'JLPT',
          component: () => import('@/views/JLPTView.vue'),
          children: [
            {
              path: ':level',
              name: 'JLPTLevel',
              component: () => import('@/views/JLPTLevelView.vue')
            }
          ]
        }
      ]
    },
    {
      path: '/WaniKani',
      name: 'WaniKani',
      component: () => import('@/views/WaniKaniView.vue')
    },
    {
      path: '/ConjugationTest',
      name: 'ConjugationTest',
      component: () => import('@/views/ConjugationTestView.vue')
    }
  ]
})

router.beforeEach(async (to, from) => {
  const jmdictStore = useJmdictStore()
  const jlptStore = useJlptStore()
  if (
    // make sure the user is authenticated
    (jmdictStore.dictionary == undefined || jlptStore.jlpt.length == 0) &&
    to.name !== 'home'
  ) {
    // redirect the user to the login page
    return { name: 'home' }
  }
})

export default router
