import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
import { concat } from 'lodash'
import type { Result } from '@/interfaces/conjugationTest'

export const useConjugationTestStore = defineStore(
  'conjugationTest',
  () => {
    // #region State
    const loadingCount = ref(0)
    const pastResults = ref<Result[]>([])
    const results = ref<Result[]>([])
    const typesToTest = ref({
      verbs: true,
      naAdjectives: false,
      iAdjectives: false
    })
    const formsToTest = ref({
      indicative: {
        plain: {
          positive: true,
          negative: true
        },
        polite: {
          positive: true,
          negative: true
        }
      },
      past_indicative: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      presumptive: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      past_presumptive: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      progressive: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      past_progressive: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      imperative: {
        abrupt: {
          positive: false,
          negative: false
        },
        plain: {
          positive: false,
          negative: false
        }
      },
      request: {
        polite: {
          positive: false,
          negative: false
        },
        honorific: {
          positive: false,
          negative: false
        }
      },
      potential: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      volitional: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      causative: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      passive: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      provisional: {
        plain: {
          positive: false,
          negative: false
        }
      },
      conditional: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      continuative: {
        plain: {
          positive: false,
          negative: false
        },
        polite: {
          positive: false,
          negative: false
        }
      },
      adverb: false,
      become: false,
      unbearably: false,
      noun: false,
      looks: false,
      too: false,
      attributive: false
    })
    const listsToTest = ref({
      JMDict: false,
      JLPT: {
        N5: true,
        N4: false,
        N3: false,
        N2: false,
        N1: false
      }
    })
    // const showFormsNotTesting = ref(false)
    // #endregion

    // #region Actions
    // async function fetch() {
    //     data.value = []
    //     loadingCount.value += 1
    //     return axios
    //         .get('', {})
    //         .then((ret: any) => {
    //             data.value = ret.data
    //             loadingCount.value -= 1
    //         })
    //         .catch((error: any) => {
    //             loadingCount.value -= 1
    //             throw error
    //         })
    // }
    async function saveResults() {
      pastResults.value = concat(pastResults.value, results.value)
      results.value = []
    }
    // #endregion

    // #region Getters
    const loading = computed(() => loadingCount.value > 0)
    // #endregion

    return {
      loading,
      saveResults,
      results,
      pastResults,
      formsToTest,
      typesToTest,
      listsToTest
    }
  },
  {
    persist: {
      paths: ['pastResults', 'formsToTest', 'listsToTest']
    }
  }
)
