// import axios from '@/axios/axios-'
import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
// const getJLPT = () => import('@/data/JMdict_jlpt.json')
import { findKey, get } from 'lodash'
import { useRoute } from 'vue-router'
const route = useRoute()

export const useJlptStore = defineStore('jlpt', () => {
  // #region State
  const loadingCount = ref(0)
  const jlpt = ref([])
  // #endregion

  // #region Actions
  // async function fetch() {
  //     data.value = []
  //     loadingCount.value += 1
  //     return axios
  //         .get('', {})
  //         .then((ret: any) => {
  //             data.value = ret.data
  //             loadingCount.value -= 1
  //         })
  //         .catch((error: any) => {
  //             loadingCount.value -= 1
  //             throw error
  //         })
  // }
  async function loadJLPT() {
    loadingCount.value += 1
    // return getJLPT().then((j: any) => {
    //   jlpt.value = j
    //   loadingCount.value -= 1
    // })
    const response = await fetch('/data/JMdict_jlpt.json')
    jlpt.value = await response.json()
    loadingCount.value -= 1
  }
  // #endregion

  // #region Getters
  const loading = computed(() => loadingCount.value > 0)
  const jlptItems = (l: string) => {
    const l2 = Number(l.replace('N', ''))
    // const level = findKey(jlpt.value, (j: any) => j.includes(l2))
    //@ts-ignore
    const level = get(jlpt.value, l2)
    return level == undefined ? null : level
  }
  // #endregion

  return { loading, loadJLPT, jlpt, jlptItems }
})
