// import axios from '@/axios/axios-'
import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
// import jmdict from "@/data/jmdict-eng-3.5.0.json"
import type { JMDict, Word, Sense } from '@/interfaces/jmdict'
// const getJMDict = () => import('@/data/jmdict-eng-3.5.0.json')
import { isKanji, isKana, toKana } from 'wanakana'
import { get, toPairs, keys, intersection, flatten, uniqBy, uniq, sample, concat } from 'lodash'
import { useJlptStore } from './jlpt'
import { useConjugationTestStore } from './conjugationTest'

const hasKanji = (input: string) => {
  let hasKanji: boolean = false
  hasKanji ||
    [...input].forEach((l: any) => {
      hasKanji = hasKanji || isKanji(l)
    })
  return hasKanji
}

export const useJmdictStore = defineStore('jmdict', () => {
  // #region State
  const loadingCount = ref(0)
  const dictionary = ref<JMDict>()
  const uglyJmdict = {
    kanji: 'a',
    kana: 'b',
    common: 'c', // -> false 0, true 1
    text: 'd',
    tags: 'e',
    appliesToKanji: 'f',
    sense: 'g',
    partOfSpeech: 'h',
    appliesToKana: 'i',
    related: 'j',
    antonym: 'k',
    field: 'l',
    dialect: 'm',
    misc: 'n',
    info: 'o',
    languageSource: 'p',
    gloss: 'q',
    lang: 'r',
    gender: 's',
    type: 't'
  }
  // #endregion

  // #region Actions
  // async function fetch() {
  //     data.value = []
  //     loadingCount.value += 1
  //     return axios
  //         .get('', {})
  //         .then((ret: any) => {
  //             data.value = ret.data
  //             loadingCount.value -= 1
  //         })
  //         .catch((error: any) => {
  //             loadingCount.value -= 1
  //             throw error
  //         })
  // }
  async function loadDictionary() {
    loadingCount.value += 1
    // return getJMDict().then((jmdict: any) => {
    //   dictionary.value = jmdict
    //   loadingCount.value -= 1
    // })
    const response = await fetch('/data/jmdict-eng-3.5.0 - ugly.json')
    dictionary.value = await response.json()
    loadingCount.value -= 1
  }

  // #endregion

  // #region Getters
  const loading = computed(() => loadingCount.value > 0)

  const findWord = (w: string) => {
    let words: Word[] = []
    if (dictionary.value == undefined || dictionary.value == null) {
      return []
    } else {
      words = [
        ...dictionary.value.words.filter((d: any) =>
          d[uglyJmdict['kanji']].map((p: any) => p[uglyJmdict['text']]).includes(w)
        ),
        ...dictionary.value.words.filter((d: any) =>
          d[uglyJmdict['kana']].map((p: any) => p[uglyJmdict['text']]).includes(w)
        )
      ]
    }
    return words
  }

  const findTag = (t: string): string => {
    if (dictionary.value == null) return ''
    const tag = get(dictionary.value.tags, t)
    return tag
  }

  const findTags = (t: string[]): string[] => {
    if (dictionary.value == null) return []
    const tags: string[] = []
    t.forEach((t: string) => {
      tags.push(findTag(t))
    })
    return tags
  }

  const findWordsByIds = (ids: number[]): Word[] => {
    if (dictionary.value == null) return []
    return dictionary.value.words.filter((w: Word) => ids.includes(Number(w.id)))
  }

  const verbIds = computed(() => {
    if (dictionary.value == null) return []
    //@ts-ignore
    const verbTags = toPairs(dictionary.value.tags)
      .filter((t: any) => t[1].indexOf('Godan verb') > -1 || t[1].indexOf('Ichidan verb') > -1)
      .map((t: any) => t[0])
    // return verbTags.map((t: any) => t[0])
    const ids = dictionary.value.words
      .filter(
        (w: Word) =>
          intersection(
            //@ts-ignore
            flatten(w[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']])),
            verbTags
          ).length > 0
      )
      .map((w: Word) => w.id)
    // return uniqBy(ids, 'id').map((w: ) =>)
    return uniq(ids)
  })
  const naAdjectiveIds = computed(() => {
    if (dictionary.value == null) return []
    const ids = dictionary.value.words
      //@ts-ignore
      .filter((w: Word) =>
        //@ts-ignore
        flatten(w[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']])).includes(
          'adj-na'
        )
      )
      .map((w: Word) => w.id)
    return uniq(ids)
  })
  const iAdjectiveIds = computed(() => {
    if (dictionary.value == null) return []
    const ids = dictionary.value.words
      //@ts-ignore
      .filter((w: Word) =>
        flatten(
          //@ts-ignore
          w[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']]).includes('adj-i')
        )
      )
      .map((w: Word) => w.id)
    return uniq(ids)
  })
  const getRandomItemToConjugate = () => {
    const jlpt = useJlptStore()
    const conjugationTestStore = useConjugationTestStore()
    let ids: string[] = []
    if (conjugationTestStore.typesToTest['verbs']) {
      ids = concat(ids, verbIds.value)
    }
    if (conjugationTestStore.typesToTest['naAdjectives']) {
      ids = concat(ids, naAdjectiveIds.value)
    }
    if (conjugationTestStore.typesToTest['iAdjectives']) {
      ids = concat(ids, iAdjectiveIds.value)
    }
    const lists = conjugationTestStore.listsToTest
    let idsFromList: string[] = []
    if (lists['JLPT']) {
      keys(lists.JLPT).forEach((key: any) => {
        //@ts-ignore
        if (lists['JLPT'][key]) {
          idsFromList = concat(
            idsFromList,
            //@ts-ignore
            jlpt.jlptItems(key).map((i: any) => i.toString())
          )
        }
      })
    }
    if (idsFromList.length > 0) {
      ids = intersection(ids, idsFromList)
    }
    const randomId = sample(ids)
    //@ts-ignore
    return dictionary.value.words.find((i: Word) => i.id == parseInt(randomId))
    // return randomId
  }
  const isWordVerb = (id: number) => {
    if (dictionary.value == null) {
      return false
    }
    const word = dictionary.value.words.find((w: Word) => w.id == id.toString())
    const verbTags = toPairs(dictionary.value.tags)
      .filter((t: any) => t[1].indexOf('Godan verb') > -1 || t[1].indexOf('Ichidan verb') > -1)
      .map((t: any) => t[0])
    return word == undefined
      ? false
      : intersection(
          //@ts-ignore
          flatten(word[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']])),
          verbTags
        ).length > 0
  }
  const isWordNaAdjective = (id: number) => {
    if (dictionary.value == null) {
      return false
    }
    const word = dictionary.value.words.find((w: Word) => w.id == id.toString())
    return word == undefined
      ? false
      : //@ts-ignore
        flatten(word[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']])).includes(
          'adj-na'
        )
  }
  const isWordIAdjective = (id: number) => {
    if (dictionary.value == null) {
      return false
    }
    const word = dictionary.value.words.find((w: Word) => w.id == id.toString())
    return word == undefined
      ? false
      : //@ts-ignore
        flatten(word[uglyJmdict['sense']].map((s: any) => s[uglyJmdict['partOfSpeech']])).includes(
          'adj-i'
        )
  }

  // #endregion

  return {
    loading,
    dictionary,
    loadDictionary,
    findWord,
    findTag,
    findTags,
    findWordsByIds,
    verbIds,
    naAdjectiveIds,
    iAdjectiveIds,
    getRandomItemToConjugate,
    isWordVerb,
    isWordNaAdjective,
    isWordIAdjective,
    uglyJmdict
  }
})
