const toastDefault = {
  theme: 'auto',
  autoClose: 3000,
  transition: toast.TRANSITIONS.BOUNCE,
  position: toast.POSITION.TOP_RIGHT
  // closeButton: closeButton
}

export default {
  install: (app: any, options: any) => {
    const notification = (message: string, type: string, options2: any) => {
      toast(message, Object.assign({}, toastDefault, { type: type }, options, options2))
    }
    app.provide('notify', {
      success: (message: string, options2: any = {}) => {
        notification(message, 'success', Object.assign({}, options2))
      },
      error: (message: string, options2: any = {}) => {
        notification(message, 'error', Object.assign({}, options2))
      },
      promise: (
        func: Function,
        pending: string,
        success: string,
        error: string,
        options2: any = {}
      ) => {
        toast.promise(
          func(),
          { pending: pending, success: success, error: error },
          Object.assign({}, toastDefault, { position: toast.POSITION.BOTTOM_CENTER }, options2)
        )
      }
    })
    // window.onunhandledrejection = async (error: any) => {
    //   // debugger
    //   notification(error, 'error', {})
    // }

    // app.config.errorHandler = async (err: any, vm: any, info: any) => {
    //   // debugger
    //   notification(err.toString(), 'error', {})
    // }

    // window.onerror = async (message: any, source: any, lineno: any, colno: any, error: any) => {
    //   // debugger
    //   notification(message, 'error', {})
    // }
  }
}
