import axiosWaniKani from '@/axios/axios-wanikani'
import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
//@ts-ignore
import type { iUser } from '@/interfaces/waniKani/user'
//@ts-ignore
import type { iSubjects } from '@/interfaces/waniKani/subjects'

export const useWaniKaniStore = defineStore(
  'waniKani',
  () => {
    // #region State
    const loadingCount = ref(0)
    const apiKey = ref('')
    const userData = ref<iUser | null>(null)
    const subjects = ref<iSubjects | null>(null)
    const loggedIn = computed(() => {
      return userData.value?.username != undefined && userData.value.username != ''
    })
    // #endregion

    // #region Actions
    function clearData() {
      userData.value = null
      subjects.value = null
    }
    function fetchUserData() {
      return new Promise((resolve, reject) => {
        loadingCount.value += 1
        const url = `user`
        axiosWaniKani
          .get(url, {})
          .then((ret: any) => {
            userData.value = ret
          })
          .catch((error: any) => {
            console.log(error.message)
          })
          .then(
            (response: any) => {
              loadingCount.value -= 1
              resolve(response) // Let the calling function know that http is done. You may send some data back
            },
            (error: any) => {
              loadingCount.value -= 1
              // http failed, let the calling function know that action did not work out
              reject(error)
            }
          )
      })
    }
    function fetchSubjects() {
      return new Promise((resolve, reject) => {
        loadingCount.value += 1
        const url = `subjects`
        axiosWaniKani
          .get(url, { params: { types: 'vocabulary' } })
          .then((ret: any) => {
            subjects.value = ret.filter(
              (v: any) =>
                v.data.parts_of_speech.includes('transitive verb') ||
                v.data.parts_of_speech.includes('intransitive verb') ||
                v.data.parts_of_speech.includes('い adjective') ||
                v.data.parts_of_speech.includes('な adjective') ||
                v.data.parts_of_speech.includes('する verb') ||
                v.data.parts_of_speech.includes('godan verb') ||
                v.data.parts_of_speech.includes('ichidan verb')
            )
          })
          .catch((error: any) => {
            console.log(error.message)
          })
          .then(
            (response: any) => {
              loadingCount.value -= 1
              resolve(response) // Let the calling function know that http is done. You may send some data back
            },
            (error: any) => {
              loadingCount.value -= 1
              // http failed, let the calling function know that action did not work out
              reject(error)
            }
          )
      })
    }
    // #endregion

    // #region Getters
    const loading = computed(() => loadingCount.value > 0)
    // #endregion

    return {
      loading,
      apiKey,
      userData,
      subjects,
      loggedIn,
      clearData,
      fetchSubjects,
      fetchUserData
    }
  },
  {
    persist: {
      paths: ['apiKey']
    }
  }
)
